import {useId,TabNavigator,View} from "vritra";
import css from "./TabNavigatorPreview.module.css";


export default function TabNavigatorPreview(props){
    const {parent,id=useId("tabNavigatorpreview")}=props;
    const tabNavigatorpreview=TabNavigator({
        parent,id,
        className:css.tabNavigatorpreview,
        headerClassName:css.header,
        containerClassName:css.container,
        tintColor:mainColor,
        tabTextColor:textColor,
        tabs:new Array(4).fill().map((_,i)=>({
            id:"tab"+(i+1),
            label:"tab "+(i+1),
            renderContent:({parent,context})=>{
                const tabview=View({parent,className:css.tabview});
                tabview.innateHTML=`
                    <p>${context.label} content</p>
                `;
                return tabview;
            },
        })),
    });

    tabNavigatorpreview.beforeEndHTML=`
    `;

    return tabNavigatorpreview;
}
