import React from "react";
import css from "./FieldView.module.css";
import SectionView from "../SectionView";
import TypeText from "../../TypeText/TypeText";
import {Badge,NoticeView,TextView} from "Components";
import {book0} from "Assets";


export default function FieldView(props){
    const {field}=props,{name,type,description,required,notice,tip}=field;
    const typename=typeof(type)==="string"?type:type?.name;
    const subfields=field.props||field.overloads;
    return (
        <div id={name} className={css.fieldview}>
            <div className={css.header}>
                <span className={css.name}>{name}</span>
                {required&&<Badge className={css.badge} text="required"/>}
                {typename&&
                    <TypeText
                        style={{color:[":","|"].some(char=>typename.includes(char))?minorColor:mainColor}}
                        type={type||"any"}
                    />
                }
            </div>
            {description&&<TextView className={css.description} text={description}/>}
            {subfields&&
                <SectionView 
                    className={css.subfields}
                    title={field.overloads&&"overloads"}
                    fields={subfields}
                />
            }
            {field.component&&<div className={css.component}><field.component field={field}/></div>}
            {(notice||tip)&&<NoticeView 
                className={css.noticeview}
                text={tip||notice} icon={tip&&book0}
            />}
            <div className={css.bordertop}/>
        </div>
    )
}
