import React from "react";
import css from "./PluginsIntro.module.css";
import {Badge,NoticeView,Title} from "Components";


export default function PluginsIntro(props){
    const {}=props;
    return (
        <div className={`${css.pluginsintro} ${props.className||""}`}>
            <Title>supported cordova platforms</Title>
            <ul className={css.platforms}>
                {statics.platfoms.map(({id,label})=>(
                    <li key={id}>
                        <span className={css.name}>Cordova-{id}</span>
                        {label&&<Badge text={label}/>}
                    </li>
                ))}
            </ul>
            <NoticeView text={`
                For Android, all plugins were tested using cordova-android version <b>10.1.2</b> .
                @please report in github any issues occured when using higher versions.
            `}/>
            {/* <h3 className={css.title}>cordova plugins</h3>
            <p>Supported platforms :</p>
            <ul className={css.platforms}>
                <li>android {">"}=10.0.0</li>
                <li>IOS</li>
                <li>browser (for development only)</li>
            </ul>
            <p>Required cordova version {">"}=10.0.0</p>
            <p>Please feel free to report any issue</p> */}
        </div>
    )
}

const statics={
    platfoms:[
        {
            id:"android",
            label:">= 10.0.0"
        },
        {id:"iOS"},
        {
            id:"browser",
            label:"dev only",
        },
    ],
}
